/* usage : 

    @media VARIABLE_NAME {
        // style in here 
    }
*/
html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0%;
}

body .wc {
  max-width: 1110px;
  width: 100%;
}
body .flexer {
  display: flex !important;
}
@media only screen and (max-width: 1179px) {
  body.MobMenue {
    overflow: hidden;
  }
  body.MobMenue .nlc {
    display: flex !important;
  }
  body.MobMenue .nlc .NavLinks {
    opacity: 1 !important;
    transition-duration: 300ms;
  }
}

#nav {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  padding: 3.4375rem;
  width: 100%;
}
@media only screen and (max-width: 1179px) {
  #nav {
    padding: 2rem 1.5rem;
  }
  #nav .nlc {
    display: none;
  }
}
#nav .flex {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}
#nav .nlc {
  width: inherit;
}
#nav .NavLinks {
  display: flex;
  flex-flow: row nowrap;
  width: 100%;
  align-items: baseline;
  margin-left: 1rem;
}
@media only screen and (max-width: 620px) {
  #nav .NavLinks {
    z-index: 3;
  }
}
@media only screen and (max-width: 1179px) {
  #nav .NavLinks {
    position: absolute;
    flex-flow: column;
    left: 50%;
    top: 5.5rem;
    transform: translateX(-50%);
    background-color: #3a3053;
    align-items: center;
    border-radius: 1rem;
    padding: 2rem;
    width: 90%;
    margin-left: 0rem;
    opacity: 0;
    transition-duration: 300ms;
    transition-delay: 10ms;
  }
}
#nav .NavLinks a {
  text-decoration: none;
  color: #9e9aa7;
  font-size: 1rem;
  letter-spacing: 0.5px;
  font-weight: 700;
  padding: 0.125rem;
  margin: 0rem 1rem;
  transition-duration: 300ms;
}
@media only screen and (max-width: 1179px) {
  #nav .NavLinks a {
    color: white;
    margin: 0.5rem 1rem;
  }
}
#nav .NavLinks a:nth-child(4) {
  margin-left: auto;
}
@media only screen and (max-width: 1179px) {
  #nav .NavLinks a:nth-child(4) {
    border-top: 1px solid gray;
    width: 100%;
    text-align: center;
    padding-top: 1rem;
  }
}
#nav .NavLinks a:nth-child(5) {
  background-color: #2acfcf;
  padding: 0.5rem 1.25rem;
  border-radius: 1.5rem;
  color: #ffffff;
}
@media only screen and (max-width: 1179px) {
  #nav .NavLinks a:nth-child(5) {
    width: 100%;
    text-align: center;
  }
}
#nav .NavLinks a:nth-child(5):hover {
  opacity: 0.7;
  color: #ffffff;
}
#nav .NavLinks a:hover {
  color: #000000;
  cursor: pointer;
}
#nav .BurgerMenue {
  cursor: pointer;
  display: none;
  margin-top: 0.25rem;
  height: fit-content;
}
@media only screen and (max-width: 1179px) {
  #nav .BurgerMenue {
    display: block;
  }
}
#nav .BurgerMenue .BurgerBars {
  width: 24px;
  height: 3px;
  background-color: #9e9aa7;
}
#nav .BurgerMenue .BurgerBars:nth-child(2) {
  margin: 6px 0px;
}

#hero {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  padding: 3.4375rem;
}
@media only screen and (max-width: 1179px) {
  #hero {
    padding: 2rem 1.5rem;
  }
}
#hero .hero {
  display: flex;
  flex-flow: row nowrap;
  align-items: flex-start;
}
@media only screen and (max-width: 1179px) {
  #hero .hero {
    flex-flow: column nowrap;
    align-items: center;
  }
}
#hero .hero .HeroInfo {
  margin-top: 3.125rem;
  width: 37rem;
  z-index: 2;
}
@media only screen and (max-width: 1179px) {
  #hero .hero .HeroInfo {
    width: 100%;
    text-align: center;
  }
}
#hero .hero .HeroInfo p {
  font-size: 1.5rem;
}
@media only screen and (max-width: 1179px) {
  #hero .hero .HeroInfo p {
    font-size: 1.25rem;
    padding-top: 1rem;
  }
}
#hero .hero .HeroInfo button {
  margin-top: 2rem;
  background-color: #2acfcf;
  color: #ffffff;
  border: none;
  padding: 1rem 3rem;
  border-radius: 2rem;
  font-weight: 700;
  font-size: 1rem;
  letter-spacing: 1px;
  transition-duration: 100ms;
}
#hero .hero .HeroInfo button:hover {
  cursor: pointer;
  opacity: 0.8;
}
#hero .hero img {
  position: absolute;
  height: 480px;
  left: 55%;
}
@media only screen and (max-width: 620px) {
  #hero .hero img {
    width: 130% !important;
    object-position: 4rem !important;
  }
}
@media only screen and (max-width: 1179px) {
  #hero .hero img {
    position: unset;
    width: 60%;
    height: auto;
    object-fit: cover;
    object-position: 0rem;
  }
}

#main {
  background-color: #f0f1f6;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}

body {
  font-family: "Poppins", sans-serif;
}

h1, h2, h3 {
  margin-top: 0px;
}

h1 {
  color: #232127;
  font-size: 5rem;
  font-weight: 700;
  line-height: 6rem;
}
@media only screen and (max-width: 1179px) {
  h1 {
    font-size: 3rem;
    line-height: 3.5rem;
  }
}

p {
  color: #9e9aa7;
}